import React from "react";
import { useState,useEffect } from "react";
import M_Sidebar from "./Sidebar";
import { Link, useNavigate } from "react-router-dom";

const DiplayServices=()=>{

    const navigate = useNavigate();

    // useEffect(()=>{
    //     const auth=localStorage.getItem('user');
    //     if(auth)
    //     {
    //         navigate('/')
    //     }
    // })

    const [service, setService] = useState([]);

    useEffect(()=>{
        getService();
    },[])

    const getService = async ()=>{
        let result = await fetch('http://localhost:5000/service');
        result = await result.json();
        setService(result);
    }

        console.warn("service",service);

    const deleteservice= async(id)=>{
        let result = fetch(`http://localhost:5000/service/${id}` ,{
            method:"Delete"
        });
        window.location.reload(false)
        result = await result.json()
        if(result)
        {
            getService();
            alert("Record is Deleted!")
        }
    }

    return(
        <div>
        <div className="layout-wrapper layout-content-navbar"> 
        <div className='layout-container'>
        <M_Sidebar/>
        <div className='layout-page'>
    
        <table>
  <tr>
    <th>Sr. No.</th>
    <th>Service Name</th>
    <th>Duration</th>
    <th>Price</th>
    {/* <th>Package</th> */}
    {/*<th>Product Brand</th>
    <th>Product Description</th>
    <th>Product Category</th> */}
    <th>Update/Delete</th>
  </tr>
  
  {
        service.map((item,index)=>
        <tr key={item._id}>
        <td>{index+1}</td>
        <td>{item.name}</td>
        {/* <td>{item.feedback}</td> */}
        <td>{item.duration}</td>
        <td>{item.price}</td>
        {/* <td>{item.brand}</td>ss
        <td>{item.description}</td>
        <td>{item.category}</td>     */}
        <td>
        <Link to={'/updateservice/'+item._id}><button>Update</button></Link>  <button onClick={()=>deleteservice(item._id)}>Delete</button></td>
        </tr>

        )
  }

</table>

</div>
</div>
</div>
</div>
    )
}

export default DiplayServices;

