import React from "react";
import { useEffect } from "react";
import M_Sidebar from "./Sidebar";
import { useNavigate } from "react-router-dom";

const AddInventory=()=>{
    const [name, setName]= React.useState('');
    const[Pur_date, setPdate]= React.useState('');
    const [Pur_place, setPplace]= React.useState('');
    const [quantity, setQuantity]= React.useState('');
    const [price, setPrice]= React.useState('');
    //const [feedback, setFeedback]= React.useState('');
    

    useEffect(()=>{},[])

    const navigate = useNavigate();

    // useEffect(()=>{
    //     const auth=localStorage.getItem('user');
    //     if(auth)
    //     {
    //         navigate('/')
    //     }
    // })

    const AddService = async  (e)=>{
        e.preventDefault()
        
        const serviceId = JSON.parse(localStorage.getItem('manager'))._id;
        let result = await fetch("http://localhost:5000/addinventory",{
            method:'POST',
            body:JSON.stringify({name,Pur_date,Pur_place,price,quantity}),
            headers:{
                "Content-Type":"application/json"
            }
        });
        
        result= await result.json();
        console.warn(result)
        alert('Record is Added')
        navigate('/inventory')
      }
    

    return(
        <div>
        
        <div className="layout-wrapper layout-content-navbar"> 
      <div className='layout-container'>
        <M_Sidebar/>
        <div className='layout-page'>
        <h1>ADD INVENTORY</h1>
            <form className="mb-3">
            <div className="card-body">
                  <div className="mb-3 row">
                    <label htmlFor="html5-text-input" className="col-md-2 col-form-label">Inventory Name</label>
                    <div className="col-md-10">
                      <input className="form-control" type="text" placeholder='Enter Your Inventory Name' value={name} onChange={(e)=>{setName(e.target.value)}} />
                    </div>
                  </div>
                  <div className="mb-3 row">
                    <label htmlFor="html5-text-input" className="col-md-2 col-form-label">Inventory Purchase Date</label>
                    <div className="col-md-10">
                      <input className="form-control" type="text" placeholder='Enter Your Product Price' value={Pur_date} onChange={(e)=>{setPdate(e.target.value)}} />
                    </div>
                  </div>
                  <div className="mb-3 row">
                    <label htmlFor="html5-text-input" className="col-md-2 col-form-label">Inventory Purchase Place </label>
                    <div className="col-md-10">
                      <input className="form-control" type="text" placeholder='Enter Your Product Price' value={Pur_place} onChange={(e)=>{setPplace(e.target.value)}} />
                    </div>
                  </div>
                  <div className="mb-3 row">
                    <label htmlFor="html5-text-input" className="col-md-2 col-form-label">Inventory Price</label>
                    <div className="col-md-10">
                      <input className="form-control" type="text" placeholder='Enter Your Product Price' value={price} onChange={(e)=>{setPrice(e.target.value)}} />
                    </div>
                  </div>
                  <div className="mb-3 row">
                    <label htmlFor="html5-text-input" className="col-md-2 col-form-label">Inventory Quantity</label>
                    <div className="col-md-10">
                      <input className="form-control" type="text" placeholder='Enter Your Product Price' value={quantity} onChange={(e)=>{setQuantity(e.target.value)}} />
                    </div>
                  </div>
                    
                    </div>
                    <button onClick={AddService} className="btn btn-primary d-grid w-100">ADD INVENTORY</button>
            </form>
        </div>
        </div>
        </div>
        </div>
    )
}

export default AddInventory