import React ,{useEffect} from "react";
import Header from "./header";
import Footer from "./footer"


const Barber = ()=>{
  const[barberlist, setBarber_list]=React.useState([]);


  useEffect( () =>{
      barbers();
  } , [])


  const barbers = async () => {
    let result = await fetch('http://localhost:5000/barber');
    result = await result.json();
    console.log(result);
    setBarber_list(result);
}
    return(
        <div>
        <Header/>
  {/* Page Header Start */}
  <div className="page-header">
    <div className="container">
      <div className="row">
        <div className="col-12">
          <h2>Barber</h2>
        </div>
        <div className="col-12">
          <a href>Home</a>
          <a href>Barber</a>
        </div>
      </div>
    </div>
  </div>
  {/* Page Header End */}
  {/* Team Start */}
  <div className="team">
    <div className="container">
      <div className="section-header text-center">
        <p>Our Barber Team</p>
        <h2>Meet Our Hair Cut Expert Barber</h2>
      </div>
      <div className="row">
      {
          barberlist.map((item ,index) =>
          <div className="col-lg-3 col-md-6">
          <div className="team-item">
            <div className="team-img">
              <img src="img/team-1.jpg" alt="Team Image" />
            </div>
            <div className="team-text">
              <h2>{item.name}</h2>
              <p>Master Barber</p>
            </div>
          </div>
        </div>
          )
        }
       
      </div>
    </div>
  </div>
  {/* Team End */}
  <Footer/>
</div>


    )
}


export default Barber;

  