import React, { useState, useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom'
import M_Sidebar from './Sidebar';



const AddManager = () => {
  const [name, setName] = useState("");
  const [gender, setGender] = useState("");
  const [dob, setDOB] = useState("");
  const [address, setAddress] = useState("");
  const [pincode, setPincode] = useState("");
  const [contactno, setContactNo] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [doj, setDOJ] = useState("");
  const [salary, setSalary] = useState("");
  const [role, setRole] = useState("manager");
  const [status, setStatus] = useState("");

  const navigate = useNavigate();
  

    // useEffect(()=>{
    //     const auth=localStorage.getItem('user');
    //     if(auth)
    //     {
    //         navigate('/')
    //     }
    // })
  

  const collectData = async () => {
    if (!name || !gender || !dob || !address || !pincode || !contactno || !email || !salary) {
      alert("Please Fill All Fields!!")
      return false;
    }
    const regname = /^[a-zA-Z]+ [a-zA-Z]+$/i;
    //const regdepartment = /^[a-zA-Z]+$/i;

    if(!regname.test(name)){
      alert('Enter Full name in Proper Format!!!!   FirstName Lastname!!!!!');
      return false;
        }

    const regemail = /^[a-zA-Z0-9+_.-]+@[a-zA-Z0-9.-]+$/;
  //const regdepartment = /^[a-zA-Z]+$/i;

if(!regemail.test(email)){
  alert('Enter Email in Proper Format!!');
  return false;
        }
    

    let result = await fetch('http://localhost:5000/registeremp', {
      method: 'POST',
      body: JSON.stringify({ name, gender, dob, address, pincode, contactno, email, password, doj, salary, role, status }),
      headers: {
        'Content-Type': 'application/json'
      },
    })

    result = await result.json()
    console.warn(result.json);
    localStorage.setItem("user", JSON.stringify(result));
    navigate('/admin')
  }
  return (
    <div>
      <div className="layout-wrapper layout-content-navbar">
        <div className='layout-container'>
          <M_Sidebar />
          <div className='layout-page'>
            <div className="container-xxl">
              <div className="authentication-wrapper authentication-basic container-p-y">
                <div className="authentication-inner">
                  {/* Register Card */}
                  <div className="card">
                    <div className="card-body">


                      <h2 className="mb-2">Add Manager</h2>

                      <form className="mb-3">
                        <div className="card-body">
                          <div className="mb-3 row">
                            <label htmlFor="html5-text-input" className="col-md-2 col-form-label">Name</label>
                            <div className="col-md-10">
                              <input className="form-control" type="text" placeholder='Enter Your Name' value={name} onChange={(e) => setName(e.target.value)} />
                            </div>
                          </div>
                          <div className="mb-3 row">
                            <label htmlFor="html5-text-input" className="col-md-2 col-form-label">Gender</label>
                            <div className="col-md-10">
                              <input className="form-check-input" type="radio" value={'m'} onChange={(e) => setGender(e.target.value)} name="inlineRadioOptions" defaultValue="option1" />
                              <label className="form-check-label" htmlFor="inlineRadio1">Male</label>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;

                              <input className="form-check-input" type="radio" name="inlineRadioOptions" value={'f'} onChange={(e) => setGender(e.target.value)} defaultValue="option2" />
                              <label className="form-check-label" htmlFor="inlineRadio2">Female</label>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;

                              <input className="form-check-input" type="radio" name="inlineRadioOptions" value={'o'} onChange={(e) => setGender(e.target.value)} defaultValue="option3" />
                              <label className="form-check-label" htmlFor="inlineRadio3">Other</label>
                            </div>
                          </div>
                          <div className="mb-3 row">
                            <label htmlFor="html5-date-input" className="col-md-2 col-form-label" >Date Of Birth </label>
                            <div className="col-md-10">
                              <input className="form-control" type="date" value={dob} onChange={(e) => setDOB(e.target.value)} />
                            </div>
                          </div>
                          <div className="mb-3 row">
                            <label htmlFor="html5-text-input" value={address} className="col-md-2 col-form-label"  >Address </label>
                            <div className="col-md-10">
                              <input className="form-control" type="text" placeholder='Enter Your Address' onChange={(e) => setAddress(e.target.value)} />
                            </div>
                          </div>
                          <div className="mb-3 row">
                            <label htmlFor="html5-text-input" className="col-md-2 col-form-label">Pin Code</label>
                            <div className="col-md-10">
                              <input className="form-control" type="text" placeholder='Enter Pin Code' value={pincode} onChange={(e) => setPincode(e.target.value)} />
                            </div>
                          </div>
                          <div className="mb-3 row">
                            <label htmlFor="html5-tel-input" className="col-md-2 col-form-label">Phone</label>
                            <div className="col-md-10">
                              <input className="form-control" type="tel" placeholder='Enter Contact No.' defaultValue="8128839008" value={contactno} onChange={(e) => setContactNo(e.target.value)} />
                            </div>
                          </div>


                          <div className="mb-3 row">
                            <label htmlFor="html5-email-input" className="col-md-2 col-form-label">Email</label>
                            <div className="col-md-10">
                              <input className="form-control" type="email" placeholder='Enter Email' defaultValue="john@example.com" value={email} onChange={(e) => setEmail(e.target.value)} />
                            </div>
                          </div>



                          <div className="mb-3 row">
                            <label htmlFor="html5-password-input" className="col-md-2 col-form-label">Salary</label>
                            <div className="col-md-10">
                              <input className="form-control" type="number" placeholder='Enter Salary' defaultValue="99999" value={salary} onChange={(e) => setSalary(e.target.value)} />
                            </div>
                          </div>

                        </div>

                        <button onClick={collectData} className="btn btn-primary d-grid w-100">Add Manager</button>
                      </form>
                    </div>
                  </div>
                  {/* Register Card */}
                </div>
              </div>
            </div>
            {/* / Content */}
          </div>
        </div>
      </div>
    </div>

  )
}

export default AddManager;
