import React from "react";
import { useEffect } from "react";
import M_Sidebar from "./Sidebar";
import { useNavigate } from "react-router-dom";
import axios from "axios";


const Addproduct=()=>{
    const [name, setName]= React.useState('');
    const [type, setType]= React.useState('');
    const [quantity, setQuantity]= React.useState('');
    const [price, setPrice]= React.useState('');
    const [brand, setBrand]= React.useState('');
    const [description, setDescription]= React.useState('');
    const [photo, setPhoto]= React.useState('');
    const [subcategoryid, setSubcategory]= React.useState('');

    const[subcategory_list,setSubcategory_list]=React.useState([{'psubcatname':'','subcategoryid':''}]);

    useEffect(()=>{
      const subcategory_list= async()=>{
        let result = await fetch('http://localhost:5000/product-sub-category');
        result = await result.json();
        setSubcategory_list(result);
      };
      subcategory_list();
    },[])


    const navigate = useNavigate();

    // useEffect(()=>{
    //     const auth=localStorage.getItem('user');
    //     if(auth)
    //     {
    //         navigate('/')
    //     }
    // })
    

    const addProduct = async  (e)=>{
      e.preventDefault()
        
        if(photo.type == "image/png" || photo.type == "image/jpg" || photo.type == "image/jpeg")
        {
                console.warn(name,type,quantity,price,brand,description,photo,subcategoryid);
              const userId = JSON.parse(localStorage.getItem('manager'))._id;
              alert(userId)

              const formData = new FormData();

              formData.append('name',name);
              formData.append('type',type);
              formData.append('quantity',quantity);
              formData.append('price',price);
              formData.append('brand',brand);
              formData.append('description',description);
              formData.append('category',subcategoryid);
              formData.append('photo',photo);

              const config = {
                Headers: {
                  'Content-Type': 'application/x-www-form-urlencoded'
                }
              }

              const result = await axios.post(
                "http://localhost:5000/add-product",
                formData,config
              );

              alert(result);
              console.warn(result)
              alert('Record is added')
              navigate('/display-product')
        }else{
          return alert("File type is not allowed")
        }

        // return console.warn(photo.type)

        

        // let result = await fetch("http://localhost:5000/add-product",{
        //     method:'POST',
        //     body:JSON.stringify({name,type,quantity,price,brand,description,category,photo}),
        //     headers:{
        //         "Content-Type":"application/json"
        //     }
        // });
        
        
      }
    

    return(
        <div>
        
        <div className="layout-wrapper layout-content-navbar"> 
      <div className='layout-container'>
        <M_Sidebar/>
        <div className='layout-page'>
        <h1>Add Product</h1>
            <form className="mb-3">
                  <div className="card-body">
                    <div className="mb-3 row">
                      <label htmlFor="html5-text-input" className="col-md-2 col-form-label">Product Name</label>
                      <div className="col-md-10">
                        <input className="form-control" type="text" placeholder='Enter Your Product Name' value={name} onChange={(e)=>{setName(e.target.value)}} />
                      </div>
                    </div>
                    <div className="mb-3 row">
                      <label htmlFor="html5-text-input" className="col-md-2 col-form-label">Product Type</label>
                      <div className="col-md-10">
                        <input className="form-control" type="text" placeholder='Enter Your Product Price' value={type} onChange={(e)=>{setType(e.target.value)}} />
                      </div>
                    </div>
                    <div className="mb-3 row">
                      <label htmlFor="html5-text-input" className="col-md-2 col-form-label">Product Quantity</label>
                      <div className="col-md-10">
                        <input className="form-control" type="text" placeholder='Enter Your Product Price' value={quantity} onChange={(e)=>{setQuantity(e.target.value)}} />
                      </div>
                    </div>
                    <div className="mb-3 row">
                      <label htmlFor="html5-text-input" className="col-md-2 col-form-label">Product Price</label>
                      <div className="col-md-10">
                        <input className="form-control" type="text" placeholder='Enter Your Product Price' value={price} onChange={(e)=>{setPrice(e.target.value)}} />
                      </div>
                    </div>
                    <div className="mb-3 row">
                      <label htmlFor="html5-text-input" className="col-md-2 col-form-label">Product Brand</label>
                      <div className="col-md-10">
                        <input className="form-control" type="text" placeholder='Enter Your Product Price' value={brand} onChange={(e)=>{setBrand(e.target.value)}} />
                      </div>
                    </div>
                    <div className="mb-3 row">
                      <label htmlFor="html5-text-input" className="col-md-2 col-form-label">Product Description</label>
                      <div className="col-md-10">
                        <input className="form-control" type="text" placeholder='Enter Your Product Price' value={description} onChange={(e)=>{setDescription(e.target.value)}} />
                      </div>
                    </div>
                    <div className="mb-3 row">
                      <label htmlFor="html5-text-input" className="col-md-2 col-form-label">Product Category</label>
                      <div className="col-md-10">
                      <select id="defaultSelect" class="form-select" value={subcategoryid} onChange={(e)=>{setSubcategory(e.target.value)}}>
                          {/* <option>Default select</option> */}
                         
                          {
                            subcategory_list.map(category =>(
                              <option value={category._id} key={category._id}>{category.psubcatname}</option>
                            ))
                          }
                        </select>
                      </div>
                    </div>
                    <div className="mb-3 row">
                      <label htmlFor="html5-text-input" className="col-md-2 col-form-label">Product Image</label>
                      <div className="col-md-10">
                      <input type="file" src="img_submit.gif" alt="Submit" width="48" height="48" onChange={(e)=>{setPhoto(e.target.files[0])}}></input>
                      </div>
                    </div>
                  
                    {/* <div className="mb-3 row">
                      <label htmlFor="html5-text-input" className="col-md-2 col-form-label">Product Image</label>
                      <div className="col-md-10">
                        <input className="form-control" type="file" placeholder='Enter Your Product Company' value={pimg} onChange={(e)=>{setimage(e.target.value)}} />
                      </div>
                    </div> */}
                    </div>
                    <button onClick={addProduct} className="btn btn-primary d-grid w-100">ADD PRODUCT</button>
            </form>
        </div>
        </div>
        </div>
        </div>
    )
}

export default Addproduct