import React, { useState, useEffect } from "react";
import { Link, Navigate, useNavigate } from 'react-router-dom'

const ForgetPw=()=>{
  const [pw,Setpw]=useState('');
  const [cpw,Setcpw]=useState('');
  
  let id = localStorage.getItem("ForgotID")
  const navigate = useNavigate();
  useEffect(() => {

    // alert(id)
  }, [])

  const resetpw = async()=>{

    const regpw = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/;

if(!regpw.test(pw)){
  alert('Minimum eight characters, at least one letter and one number!!!');
  return false;
        }

if(!regpw.test(cpw)){
  alert('Minimum eight characters, at least one letter and one number!!!');
  return false;
        }
      // alert(pw)
      // alert(cpw)
      let result = ""
      let newpassword = pw
      let oldpassword = cpw
      if (pw == cpw){
          result = await fetch('http://localhost:5000/forgotpasswordupdate', {
          method: 'put',
          body: JSON.stringify({id, newpassword, oldpassword}),
          headers: {
              'Content-Type': "application/json"
          }
      });
      }else{
        return alert("The new password and confirm password is not match")
      }
      
    result = await result.json();
    alert(JSON.stringify(result))
    if(result){
      localStorage.clear()
      navigate('/Login');
    }
    else{
      alert("The password is not Updated")
    }
      if(pw === cpw){
          alert('Correct')
      }
      else{
        alert('Please Enter Correct')
      }

  }
    return(
        <div>
        
  
        <div>
        <div className="container text-center mb-5 mt-5">
          <div className="row">
            <div className="col-md-12">
            </div>
          </div>
        </div>
        <div className="container">
          <div className="row">
            <div className="col-md-12">
              <div className="form-group text-center">
                <div className="card">
                  <div className="card-body">
                  <h3><b>Reset Password</b></h3>
                    <label className="margin-align">To reset the password please fill all the fields</label>
                    <div className="form-group-input otp-form-group mb-3">
                      <input type="password" placeholder='Enter Your New Password' onChange={(e)=>Setpw(e.target.value)} className="form-control fpw-inputbar" value={pw} />
                    </div>
                    <div className="form-group-input otp-form-group mb-3">
                      <input type="password" placeholder='Re-Enter Your New Password'  onChange={(e)=>Setcpw(e.target.value)} value={cpw} className="form-control fpw-inputbar"/>
                    </div>
                    <button className='otp-button' onClick={resetpw}>Reset Password</button><br /><br />
                  </div>
                </div>

              </div>
            </div>
          </div>
        </div>
        <div id="errmsg" className="text-center" />
      </div>
  </div>
    )
}

export default ForgetPw;