import React from "react";
import { useEffect } from "react";
import M_Sidebar from "./Sidebar";


const Addproduct_subcategory=()=>{
    const [psubcatname, setSubcategory_name]= React.useState('');
    const [categoryid, setCategory_id]= React.useState('');
    const[category_list,setCategory_list]=React.useState([{'pcatname':'','category_id':''}]);
   
    useEffect(()=>{
        const category_list= async()=>{
          let result = await fetch('http://localhost:5000/product-category');
          result = await result.json();
          setCategory_list(result);
        };
        category_list();
       
    },[])


    const addProduct_subcategory = async  ()=>{
        let presult = await fetch("http://localhost:5000/product-sub-category",{
            method:'POST',
            body:JSON.stringify({psubcatname,categoryid}),
            headers:{
                "Content-Type":"application/json"
            }
        });
        presult= await presult.json();
        console.warn(presult)
      }
   


    return(
        <div>
       
        <div className="layout-wrapper layout-content-navbar">
      <div className='layout-container'>
        <M_Sidebar/>
        <div className='layout-page'>
        <h1>Add Product Sub-Category</h1>
            <form className="mb-3">
                  <div className="card-body">
                    <div className="mb-3 row">
                      <label htmlFor="html5-text-input" className="col-md-2 col-form-label">Product Sub category Name</label>
                      <div className="col-md-10">
                        <input className="form-control" type="text" placeholder='Enter Your Product Name' value={psubcatname} onChange={(e)=>{setSubcategory_name(e.target.value)}} />
                      </div>
                    </div>
                    <div className="mb-3 row">
                      <label htmlFor="html5-text-input" className="col-md-2 col-form-label">Product category</label>
                      <div className="col-md-10">
                      <select id="defaultSelect" class="form-select" value={categoryid} onChange={(e)=>{setCategory_id(e.target.value)}}>
                          {/* <option>Default select</option> */}
                         
                          {
                            category_list.map(category =>(
                              <option value={category._id} key={category._id}>{category.pcatname}</option>
                            ))
                          }
                        </select>
                        {/* <input className="form-control" type="text" placeholder='Enter Your Product Name' value={category_id} onChange={(e)=>{setCategory_id(e.target.value)}} /> */}
                      </div>
                    </div>
                    </div>
                    <button onClick={addProduct_subcategory} className="btn btn-primary d-grid w-100">ADD </button>
            </form>
        </div>
        </div>
        </div>
        </div>
    )
}


export default Addproduct_subcategory

