import React from "react";
import { useState,useEffect } from "react";
import M_Sidebar from "./Sidebar";
import { useNavigate,Link } from "react-router-dom";

const ProductList=()=>{

    const [product_cat, setProducts] = useState([]);

    const navigate = useNavigate();

    // useEffect(()=>{
    //     const auth=localStorage.getItem('user');
    //     if(auth)
    //     {
    //         navigate('/')
    //     }
    // })

    useEffect(()=>{
        getProducts();
    },[])

    const getProducts = async ()=>{
        let result = await fetch('http://localhost:5000/products');
        result = await result.json();
        setProducts(result);
    }

        console.warn("products",product_cat);

    

    const deleteproduct= async(id)=>{
        let result = fetch(`http://localhost:5000/products/${id}` ,{
            method:"Delete"
        });
        window.location.reload(false)
        result = await result.json()
        if(result)
        {
            getProducts();
            alert("Record is Deleted!")
        }
    }

    return(
        <div>
        <div className="layout-wrapper layout-content-navbar"> 
        <div className='layout-container'>
        <M_Sidebar/>
        <div className='layout-page'>
    
        <table>
  <tr>
    <th>Sr. No.</th>
    <th>Product Name</th>
    <th>Product Type</th>
    <th>Product Quantity</th>
    <th>Product Price</th>
    <th>Product Brand</th>
    <th>Product Description</th>
    <th>Product Category</th>
    <th>Product Image</th>
    <th>Update/Delete</th>
  </tr>
  
  {
        product_cat.map((item,index)=>
        <tr key={item._id}>
        <td>{index+1}</td>
        <td>{item.name}</td>
        <td>{item.type}</td>
        <td>{item.quantity}</td>
        <td>{item.price}</td>
        <td>{item.brand}</td>
        <td>{item.description}</td>
        <td>{item.subcategoryid}</td>  
        <td><img src={`http://localhost:5000/${item.photo}`} height={100} width={200}/></td>
        <td>
        <Link to={'/updateproduct/'+item._id}><button>Update</button></Link> 
        <button onClick={()=>deleteproduct(item._id)}>Delete</button></td>
        </tr>

        )
  }

</table>

</div>
</div>
</div>
</div>
    )
}

export default ProductList;

