import React, { useState, useEffect } from 'react';
import { json, Link, useNavigate } from 'react-router-dom'


const Forgotpw = () => {

  const [email,setEmail] = useState('');
  const navigate = useNavigate();

  const handleforgetpw = async()=>{
    //alert(email)
    var result = await fetch('http://localhost:5000/verifyemail',{
      method:"post",
      body:JSON.stringify({email}),
      headers:{
        'Content-Type':'application/json'
      }
    })
    result = await result.json();

    const regemail = /^[a-zA-Z0-9+_.-]+@[a-zA-Z0-9.-]+$/;

    if(!regemail.test(email)){
      alert('Enter Email in Proper Format!!');
      return false;
            }
    
    if(result.result == "No User Found"){
      alert(JSON.stringify(result.result))
    }
    else{
      navigate('/otpverification')
    }
    // data = await data.JSON;
    // alert(data)
    
  }

  return (
    <div>
      <div>
        <div className="container text-center mb-5 mt-5">
          <div className="row">
            <div className="col-md-12">

              <h3><b>Forgot Password</b></h3>
            </div>
          </div>
        </div>
        <div className="container">
          <div className="row">
            <div className="col-md-12">
              <div className="form-group text-center">
                <div className="card">
                  <div className="card-body">
                    <label className="margin-align">To reset the password please type the email</label>
                    <div className="form-group-input otp-form-group mb-3">
                      <input type="email" placeholder='Enter Your Email' onChange={(e)=>setEmail(e.target.value)} value={email} className="form-control fpw-inputbar" />
                    </div>
                    <button className='otp-button' onClick={handleforgetpw}>Send OTP</button><br /><br />
                    {/* <div className="form-group-input otp-form-group mb-3">
                      <input type="text" placeholder='Enter Your OTP' className="form-control fpw-inputbar" />
                    </div>
                    <button className='otp-button'>Submit</button><br /><br /> */}

                  </div>
                </div>

              </div>
            </div>
          </div>
        </div>
        <div id="errmsg" className="text-center" />
      </div>


    </div>


  )
}



export default Forgotpw;
