import React, { useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import Header from "./header";
import Footer from "./footer"


const Appointment = () => {
  const [servicelist, setService_list] = React.useState([]);
  const [barberlist, setBarber_list] = React.useState([]);
  const [barber, setBarber] = React.useState('');
  const [service, setService] = React.useState([]);
  const [adate, setAdate] = React.useState('');
  const [atime, setTime] = React.useState('');
  const [payment, setPayment] = React.useState('');
  const [customerid,setCustomer]=React.useState('');
  const navigate = useNavigate();
  let p1
  const itemSet = (localStorage.getItem('p1') === null);


  if (itemSet) {
    //alert("bolo")
    localStorage.setItem("p1", "0")
  }
  const get_service = (e) => {
    let data = service;
    const { value, checked } = e.target
    //alert(value )
    if (checked) {

      p1 = localStorage.getItem('p1')
      p1 = parseInt(p1, 10)
      let p2 = value
      p2 = parseInt(p2, 10)
      p1 = p1 + p2
      
      document.getElementById("total").innerHTML = p1;
      localStorage.setItem("p1", p1)
      // data.push(e.target.value);
      //alert(data)
      const service_name = async () => {
        let result = await fetch(`http://localhost:5000/service_name/${value}`);
        result = await result.json();
        data.push(result);
        //alert(data)


      }
      service_name() //service=result


    }
    else {
      p1 = localStorage.getItem('p1')
      p1 = parseInt(p1, 10)
      let p2 = value
      p2 = parseInt(p2, 10)
      p1 = p1 - p2
      localStorage.setItem("p1", p1)
      document.getElementById("total").innerHTML = p1;
      const service_name = async () => {
        let result = await fetch(`http://localhost:5000/service_name/${value}`);
        result = await result.json();
        data.pop(result);
        //alert(data)
      }
      service_name() //service=result

    }
    setService(data);
    //console.log(data)
  }


  useEffect(() => {
    services();
    barbers();
    //localStorage.setItem("p1","0")
    setTime(localStorage.getItem("atime"));
    setAdate(localStorage.getItem("adate"));
    // setPrice(localStorage.setItem("price"));
    setCustomer(localStorage.getItem("userid"));
  }, [])


  const services = async () => {
    let result = await fetch('http://localhost:5000/service');
    result = await result.json();
    //console.log(result);
    setService_list(result);
  }


  const barbers = async () => {
    let result = await fetch('http://localhost:5000/barber');
    result = await result.json();
    //console.log(result);
    setBarber_list(result);
  }

  const handlepayment = async () => {
    // try{
    //   let result = "http://localhost:5000/employees";
    //   const {data} = await axios.post()
    // }
    // catch(error){
    //   console.log(error)
    // }
  }




  const book_appointment = async (e) => {
    e.preventDefault()
  
    //  alert(customerid)
    if(!service || !barber)
    {
      alert('Please Fill All Fields!!')
    }
    else{
      let presult = await fetch("http://localhost:5000/book_appointment", {
      method: 'POST',
      body: JSON.stringify({ customerid,adate, atime, barber, service,payment }),
      headers: {
        "Content-Type": "application/json"
      }
    });
    presult = await presult.json();
    localStorage.removeItem("p1");
    if(payment === "sucsess")
    {
      navigate('/payment')
    }
    else{
      navigate('/view_appointment')
    }  
    
    window.location.reload(false)
    }

    //localStorage.clear();
    //console.warn(presult)
  }


  return (
    <div>
      <Header />
      {/* Page Header Start */}
      <div className="page-header">
        <div className="container">
          <div className="row">
            <div className="col-12">
              <h2>Book Appointment</h2>
            </div>
            <div className="col-12">
              <a href>Home</a>
              <a href>Appointment</a>
            </div>
          </div>
        </div>
      </div>
      {/* Page Header End */}
      {/* Pricing Start */}
      <form>
        <section className="shopping-cart spad">
          <div className="container">
            <div className="row">
              <div className="col-lg-8">
                <div className="shopping__cart__table">
                  <table>
                    <thead>
                      <tr>
                        <th>Service</th>
                        <th>Price</th>
                        <th>Select</th>
                        <th />
                      </tr>
                    </thead>
                    <tbody>
                      {
                        servicelist.map((item, index) =>
                          <tr>
                            <td className="product__cart__item">
                              <div className="product__cart__item__pic">
                                {/* <img src="img/shopping-cart/cart-1.jpg" alt /> */}
                              </div>
                              <div className="product__cart__item__text">
                                <h6>{item.name}</h6>


                              </div>
                            </td>
                            <td className="cart__price">{item.price}</td>


                            <td className="quantity__item">
                              <div className="quantity">
                                <div className="pro-qty-2">
                                  <input type="checkbox" id="service" value={item.price} key={item.price} onChange={(e) => get_service(e)} />
                                </div>
                              </div>
                            </td>


                          </tr>




                        )
                      }
                    </tbody>
                  </table>
                </div>
                <div className="row">
                  <div className="col-lg-6 col-md-6 col-sm-6">
                    <div className="continue__btn">


                      
                    </div>
                  </div>
                  {/* <div className="col-lg-6 col-md-6 col-sm-6">
            <div className="continue__btn update__btn">
              <a href="#"><i className="fa fa-spinner" /> Update cart</a>
            </div>
          </div> */}
                </div>
              </div>
              <div className="col-lg-4">
                <div className="cart__discount">
                  <h6> Select Barber</h6>
                 
                    <select id="defaultSelect" class="form-select" value={barber} onChange={(e) => { setBarber(e.target.value) }}>
                      {/* <option>Default select</option> */}


                      {
                        barberlist.map(category => (
                          <option value={category._id} key={category._id}>{category.name}</option>
                        ))
                      }
                    </select>
                   
                </div>
                <div className="cart__total">
                  <h6>Cart total</h6>
                  <ul>
                    <li>Subtotal <span id="total">{localStorage.getItem('p1')}Rs.</span></li>


                    {/* <li>Total <span>1</span></li>   */}


                  </ul>
                  <button type="button" className="btn btn-primary" data-bs-toggle="modal" data-bs-target="#modalCenter" > Book</button>
                </div>
              </div>
            </div>
          </div>
        </section>
        {/* Vertically Centered Modal */}
        <div className="col-lg-4 col-md-6">
            <div className="modal fade" id="modalCenter" tabIndex={-1} aria-hidden="true">
              <div className="modal-dialog modal-dialog-centered" role="document">
                <div className="modal-content">
                  <div className="modal-header">
                    <h5 className="modal-title" id="modalCenterTitle">Payment</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close" />
                  </div>
                  <div className="modal-body">
                    <div className="row">
                      <div className="col mb-3">
                        <label htmlFor="nameWithTitle" className="form-label">Select Mode </label>
                        {/* <input name="default-radio-1" class="form-check-input" type="radio" value={'9'} onChange={(e)=>setPayment(e.target.value)} id="defaultRadio1" />
                        <label class="col-md-2 col-4 my-1 px-2 btn-primary" for="defaultRadio1"><div className="cell py-1" >Online</div> </label>


                        <input name="default-radio-1" class="form-check-input" type="radio" value={'10'} onChange={(e)=>setPayment(e.target.value)} id="defaultRadio1" />
                        <label class="col-md-2 col-4 my-1 px-2 btn-primary" for="defaultRadio1"><div className="cell py-1" >Cash</div> </label> */}


                       {/*  <input type="text" value={payment} onChange={(e) => {setPayment(e.target.value)}}/> */}
                        <select id="defaultSelect" class="form-select" value={payment} onChange={(e) => { setPayment(e.target.value)}}>
                          <option >Select </option>
                          <option value={'sucsess'} onClick={handlepayment} >Online</option>
                          <option value={'pending'} >cash on Shop</option>
                    </select>
                      </div>
                    </div>
                   
                  </div>
                  <div className="modal-footer">
                    <button type="button" className="btn btn-outline-secondary" data-bs-dismiss="modal">
                      Close
                    </button>
                    <button  onClick={book_appointment} className="btn btn-primary">Submit</button>
                  </div>
                </div>
              </div>
            </div>
          </div>
       


      </form>
      <Footer />
    </div>


  )
}


export default Appointment;

