// import { DatePicker } from 'react-responsive-datepicker'
// import 'react-responsive-datepicker/dist/index.css'  
function formatDate(date) {
    var d = new Date(date),
        month = '' + (d.getMonth() + 1),
        day = '' + d.getDate(),
        year = d.getFullYear();


    if (month.length < 2)
        month = '0' + month;
    if (day.length < 2)
        day = '0' + day;


    return [year, month, day].join('-');
}


function status(s) {
    var msg="";
    if ( s == 0)
        msg="Not Approve"
    else
        msg="Approve"


    return msg;
}
 


